@font-face {
  font-family: "Sora";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Sora-Regular.woff2") format("woff2"), url("fonts/Sora-Regular.woff") format("woff");
}
@font-face {
  font-family: "Sora";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Sora-Bold.woff2") format("woff2"), url("fonts/Sora-Bold.woff") format("woff");
}
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  display: grid;
  grid-template-columns: 400px 1fr;
  grid-template-rows: 50px 1fr 60px;
  max-width: 1200px;
  margin: 0 auto;
  font-family: "Sora", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 400;
  color: #000000;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.container {
  padding: 0 37px;
}

.heading {
  margin: 0;
  margin-bottom: 10px;
  font-size: 20px;
  text-transform: uppercase;
}

.card {
  background-color: #cfced4;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
  cursor: pointer;
}

.card:hover {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}

.card__bottom {
  display: grid;
  grid-template-rows: 25px 50px 25px minmax(58px, auto);
  padding: 20px;
}

.card__heading {
  margin: 0;
}

.card__image {
  width: 100%;
  object-fit: cover;
}

.card__heading-link {
  color: inherit;
  text-decoration: none;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}

.card__heading-link:hover {
  color: #747474;
}

.card__description {
  margin: 0;
}

.card__link {
  color: inherit;
  text-decoration: none;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}

.card__link:hover {
  color: #747474;
}

.card__techs {
  margin: 0;
}

.card__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 310px;
}

.card__item {
  align-self: start;
  background-color: #87cefa;
  margin-bottom: 5px;
  padding: 0 5px;
  border-radius: 10px;
}

.card__item:not(:last-child) {
  margin-right: 10px;
}

pre {
  padding: 20px;
  background-color: #d3d3d3;
}

code {
  font-size: 14px;
}

.code__example {
  display: none;
}
@media (min-width: 767px) {
  .code__example {
    width: 100%;
    display: block;
    object-fit: cover;
  }
}

.contacts__list {
  margin-bottom: 30px;
}

.contacts__item {
  display: flex;
  align-items: center;
  line-height: 30px;
}

.contacts__link {
  position: relative;
  margin: 0;
  padding-left: 10px;
  text-decoration: none;
  color: inherit;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}

.contacts__link:hover {
  color: #747474;
}

.self-education__link {
  text-decoration: none;
  color: inherit;
}

.self-education__link:hover {
  color: #747474;
}

.self-education__badge {
  display: block;
}
@media (min-width: 767px) {
  .self-education__badge {
    width: 400px;
  }
}

.self-education__year {
  margin: 0;
}

.self-education__item:not(:last-child) {
  margin-bottom: 15px;
}

.expirience__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 20px 20px;
  grid-auto-flow: row;
  margin-bottom: 30px;
}
@media (min-width: 767px) {
  .expirience__list {
    grid-template-columns: 1fr;
  }
}

.footer {
  grid-column: span 2;
  color: #ffffff;
  background-color: #3d3d3f;
}

.footer__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.footer__link {
  display: block;
}

.header {
  display: flex;
  max-width: 1200px;
  color: #ffffff;
  padding: 0 20px;
  grid-column: span 2;
  background-color: #3d3d3f;
  justify-content: space-between;
  align-items: center;
}

.header__list {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.header__item:not(:last-child) {
  margin-right: 30px;
}

.header__link {
  color: inherit;
  text-decoration: none;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}

.header__link:hover {
  color: #cfcfcf;
}

.index-main {
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: #e9e8ed;
}

.index-main .heading {
  position: relative;
  padding-bottom: 20px;
}

.index-main .heading::before {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(to right, #4c4b4e 80px, #828085 80px);
}

.intresting__list {
  margin-bottom: 30px;
}

.intresting__item {
  display: flex;
  align-items: center;
  line-height: 30px;
}

.intresting__desc {
  padding-left: 10px;
}

.main-info__avatar-container {
  width: 200px;
  margin: 0 auto;
  margin-bottom: 10px;
}

.main-info__avatar {
  border-radius: 10px;
}

.main-info__full-name {
  margin: 0;
  margin-bottom: 5px;
  font-size: 32px;
  text-transform: uppercase;
  text-align: center;
}

.main-info__specialization {
  margin: 0;
  margin-bottom: 30px;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
}

.main-nav {
  display: flex;
  height: 100%;
  align-items: center;
}

.short-info {
  padding-top: 40px;
  background: linear-gradient(to bottom, #3d3d3f 180px, #e9e8ed 180px, #e9e8ed 185px, #cfced4 185px);
}

.skills__list {
  margin-bottom: 30px;
}

.skills__item {
  display: flex;
  align-items: center;
  line-height: 30px;
  cursor: default;
}

.skills__desc {
  margin: 0;
  padding-left: 10px;
}

.change-language__button {
  font-size: 17px;
  color: #ffffff;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.change-language__button--active {
  color: #ff6347;
}

.downloadcv__link {
  display: flex;
  align-items: center;
  text-decoration: none;
  line-height: 30px;
  color: inherit;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.downloadcv__link:hover {
  color: #747474;
}

.downloadcv__desc {
  padding-left: 10px;
}